"use client";

import { useEffect, useState } from 'react';
import Link from 'next/link';
import Card from '../../components/Card';
import Button from '../../components/Button';
import Spinner from '../../components/Spinner';

interface Doctor {
  _id: string;
  full_name: string;
  specializations: string[];
  clinic_affiliations?: any[];
}

export default function DoctorsPage() {
  const [doctors, setDoctors] = useState<Doctor[]>([]);
  const [filteredDoctors, setFilteredDoctors] = useState<Doctor[]>([]);
  const [specialization, setSpecialization] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    fetchDoctors();
  }, []);

  const fetchDoctors = async () => {
    try {
      setLoading(true);
      const response = await fetch('/api/doctors');
      if (!response.ok) throw new Error('Failed to load doctors');
      const data = await response.json();
      setDoctors(data);
      setFilteredDoctors(data);
    } catch (err: any) {
      setError(err.message || 'Error fetching doctors');
    } finally {
      setLoading(false);
    }
  };

  const handleFilter = async (spec: string) => {
    setSpecialization(spec);

    if (!spec) {
      setFilteredDoctors(doctors);
      return;
    }

    try {
      const response = await fetch(`/api/doctors/search?specialization=${encodeURIComponent(spec)}`);
      if (!response.ok) throw new Error('Search failed');
      const data = await response.json();
      setFilteredDoctors(data);
    } catch (err) {
      console.error('Error searching doctors:', err);
    }
  };

  return (
    <div className="min-h-screen bg-slate-50">
      <div className="max-w-7xl mx-auto px-4 lg:px-8 py-16 lg:py-20">
        <div className="mb-16 space-y-3">
          <h1 className="text-4xl lg:text-5xl font-bold text-slate-900">Find a Doctor</h1>
          <p className="text-lg text-slate-600 max-w-2xl">Search verified healthcare professionals by specialization.</p>
        </div>

        <div className="mb-12">
          <label className="block text-sm font-semibold text-slate-700 mb-4">Search by Specialization</label>
          <input
            type="text"
            placeholder="e.g., Cardiology, Dermatology, Dentistry..."
            value={specialization}
            onChange={(e) => handleFilter(e.target.value)}
            className="w-full md:max-w-md px-4 py-3.5 rounded-lg border border-slate-200 bg-white focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent transition-all text-base"
          />
        </div>

        <div className="text-slate-600 font-medium mb-10">{filteredDoctors.length} doctors found</div>

        {loading ? (
          <div className="text-center py-20"><Spinner className="h-8 w-8" /></div>
        ) : error ? (
          <div className="bg-red-50 border border-red-200 rounded-lg p-8 text-red-700 text-center font-medium">{error}</div>
        ) : filteredDoctors.length === 0 ? (
          <Card className="text-center bg-slate-100 border border-slate-200 py-12">
            <p className="text-slate-600 text-lg">No doctors found. Try a different specialization.</p>
          </Card>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredDoctors.map((doctor) => (
              <Link key={doctor._id} href={`/doctors/${doctor._id}`} className="group">
                <Card className="h-full hover:shadow-xl transition-all duration-200 hover:scale-105 p-7 space-y-5 flex flex-col">
                  <div className="flex items-start gap-4">
                    <div className="w-14 h-14 rounded-full bg-indigo-100 flex items-center justify-center text-3xl group-hover:bg-indigo-200 transition-colors shrink-0">üë®‚Äç‚öïÔ∏è</div>
                    <div className="flex-1">
                      <h2 className="text-lg font-bold text-slate-900 group-hover:text-indigo-600 transition-colors">
                        Dr. {doctor.full_name}
                      </h2>
                      <p className="text-sm text-slate-500 mt-1">Medical Professional</p>
                    </div>
                  </div>

                  <div className="space-y-3 flex-1">
                    <div>
                      <p className="text-sm font-semibold text-slate-700 mb-3">Specializations</p>
                      <div className="flex flex-wrap gap-2">
                        {(doctor.specializations || ['General Practice']).slice(0, 2).map((spec, idx) => (
                          <span key={idx} className="inline-flex items-center rounded-full bg-indigo-50 text-indigo-700 px-3 py-1.5 text-xs font-semibold border border-indigo-200">
                            {spec}
                          </span>
                        ))}
                        {doctor.specializations && doctor.specializations.length > 2 && (
                          <span className="inline-flex items-center rounded-full bg-slate-100 text-slate-700 px-3 py-1.5 text-xs font-semibold">
                            +{doctor.specializations.length - 2}
                          </span>
                        )}
                      </div>
                    </div>

                    {doctor.clinic_affiliations && doctor.clinic_affiliations.length > 0 && (
                      <div className="text-xs text-slate-600 font-medium">
                        üè• {doctor.clinic_affiliations.length} clinic{doctor.clinic_affiliations.length !== 1 ? 's' : ''}
                      </div>
                    )}
                  </div>

                  <div className="pt-4 border-t border-slate-200 flex items-center justify-between">
                    <span className="text-sm font-semibold text-slate-700">View Profile</span>
                    <span className="text-indigo-600 font-bold group-hover:translate-x-1 transition-transform">‚Üí</span>
                  </div>
                </Card>
              </Link>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
